import { inject, reactive, ref } from "vue";

export function useFertilizationTableInit() {
    const days = reactive([
        "2021-07-06",
        "2021-07-07",
        "2021-07-08",
        "2021-07-09",
        "2021-07-10",
        "2021-07-11",
        "2021-07-12",
        "2021-07-13"
    ]);

    const fertilizationTable = ref([
        {
            employeeId: 12,
            day0Maturity: 1,
            day0IVFICSI: 2,
            day1Stage: null,
            day1Grade: null,
            day2Stage: null,
            day2Grade: null,
            day3Stage: null,
            day3Grade: null,
            day4Stage: null,
            day4Grade: null,
            day5Stage: null,
            day5Grade: null,
            day6Stage: null,
            day6Grade: null,
            day7Stage: null,
            day7Grade: null,
            comment: null,
            fate: null,
            straw: null,
            frozenDate: null,
            thawingDate: null,
            image: null
        }
    ]);

    const embryoDetailDialog = inject("embryoDetailDialog");

    const openEmbryoDetailDialog = inject("openEmbryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    return {
        days,
        fertilizationTable,
        embryoDetailDialog,
        openEmbryoDetailDialog,
        closeEmbryoDetailDialog
    };
}

export function useEmbryoDetailDialogInit() {
    const embryoDetailDialog = inject("embryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    return { embryoDetailDialog, closeEmbryoDetailDialog };
}
